import http from "../http-common";

class weatherDataService {
  csv(data,id) {
    return http.post(`/csv/upload/${id}`,data);
  }
  getAll() {
    return http.get("/csv/model/show");
  }
  model(data){
    return http.post(`/csv/upload/model`,data)
  }
  get(id) {
    return http.get(`/csv2/${id}`);
  }
  create(data) {
    return http.post("/csv2", data);
  }
  findDate(data,data2){
    return http.get(`/date/${data},${data2}`)
  }
  update(id, data) {
    return http.put(`/csv2/${id}`, data);
  }

  Delete(id) {
    return http.delete(`/csv/Delete_model/${id}`);
  }

  DeleteAll() {
    return http.delete(`/csv/Delete_model`);
  }

  // findByTitle(title) {
  //   return http.get(`/csv?title=${title}`);
  // }
}

export default new weatherDataService();